import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductListComponent } from './productlist.component';
import { AddeditproductComponent } from './addeditproduct.component';
import { ViewproductComponent } from './viewproduct.component';

const routes: Routes = [
  { path: '', component: ProductListComponent },
  { path: 'addProduct', component: AddeditproductComponent },
  { path: 'viewProduct/:id', component: AddeditproductComponent },
  { path: 'viewproducts/:id', component: ViewproductComponent },
  { path: 'editProduct/:Id', component: AddeditproductComponent },
];
   
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProductListRoutingModule { }
